// 
import corePlugin from './plugin/core'
// 
import Tree from './tree'
// 
import "@/styles/index.scss";
import "@/styles/checkbox.scss";

const install = function (Vue) {
  Vue.component(Tree.name, Tree)
  Vue.use(corePlugin)
}

/* istanbul ignore if */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install, 
  Tree
}
